@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }

  @include onDesktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

@mixin pageGrid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
  }
}

@mixin hover($property, $value) {
  transition: #{$property} 0.3s;

  &:hover {
    #{$property}: $value;
  }
}

@mixin form-width {
  width: 280px;

  @include onTablet {
    width: 288px;
  }

  @include onDesktop {
    width: 411px;
  }
}
